import { Preloader } from "./Preloader.js";
const ASSET_PATH = "../assets/";

class Game {
    constructor() {
        this.modes = Object.freeze({
            NONE: Symbol("none"),
            PRELOAD: Symbol("preload"),
            INITIALISING: Symbol("initialising"),
            CREATING_LEVEL: Symbol("creating_level"),
            ACTIVE: Symbol("active"),
            GAMEOVER: Symbol("gameover")
        });

        this.mode = this.modes.NONE;
        this.container;
        this.player = {};
        this.camera;
        this.scene;
        this.renderer;
        this.cameraFade = 0.05;
        this.container = document.createElement("div");
        this.container.style.height = "100%";
        document.body.appendChild(this.container);

        const game = this;
        this.anims = [
            "ascend-stairs",
            "gather-objects",
            "look-around",
            "push-button",
            "run"
        ];

        this.mode = this.modes.PRELOAD;
        this.clock = new THREE.Clock();

        const options = {
            assets: [],
            oncomplete: function () {
                game.init();
                game.animate();
            }
        };
        this.anims.forEach(function (anim) {
            options.assets.push(`${ASSET_PATH}fbx/${anim}.fbx`);
        });

        // const preloader = new Preloader(options);
        this.init()
        this.animate()
    }

    init() {
        this.mode = this.modes.INITIALISING;
        this.camera = new THREE.PerspectiveCamera(
            75,
            window.innerWidth / window.innerHeight,
            1,
            2000
        );
        let col = 0x605050;
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(col);
        this.scene.fog = new THREE.Fog(col, 500, 1500);

        let light = new THREE.HemisphereLight(0xffffff, 0x444444);
        light.position.set(0, 200, 0);
        this.scene.add(light);

        light = new THREE.DirectionalLight(0xffffff);
        light.position.set(0, 200, 100);
        light.castShadow = true;
        light.shadow.mapSize.width = 2048;
        light.shadow.mapSize.height = 2048;
        light.shadow.camera.top = 3000;
        light.shadow.camera.bottom = -3000;
        light.shadow.camera.left = -3000;
        light.shadow.camera.right = 3000;
        light.shadow.camera.far = 3000;
        this.scene.add(light);

        const loader = new THREE.FBXLoader();
        const game = this;

        console.log("wsh, ", `${ASSET_PATH}fbx/girl-walk.fbx`)
        game.loadEnvironment(loader)
        loader.load(
            `${ASSET_PATH}fbx/girl-walk.fbx`,
            function (object) {
                object.mixer = new THREE.AnimationMixer(object);
                object.mixer.addEventListener("finished", function (e) {
                    game.action = "look-around";
                });
                object.castShadow = true;

                game.player.mixer = object.mixer;
                game.player.root = object.mixer.getRoot();

                object.name = "Character";

                object.traverse(function (child) {
                    if (child.isMesh) {
                        child.castShadow = true;
                        child.receiveShadow = true;
                    }
                });

                game.scene.add(object);
                game.player.object = object;
                game.player.walk = object.animations[0];
                console.log("girl, ", object)

                game.createCameras();
                game.loadEnvironment(loader);
            },
            null,
            this.onError
        );
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap; // default THREE.PCFShadowMap
        this.renderer.shadowMapDebug = true;
        this.container.appendChild(this.renderer.domElement);
        // stats = new Stats();

        window.addEventListener(
            "resize",
            function () {
                game.onWindowResize();
            },
            false
        );
    }

    createCameras() {
        const front = new THREE.Object3D();
        front.position.set(112, 100, 200);
        front.parent = this.player.object;
        const back = new THREE.Object3D();
        back.position.set(0, 400, -250);
        back.parent = this.player.object;
        const wide = new THREE.Object3D();
        wide.position.set(178, 139, 465);
        wide.parent = this.player.object;
        const overhead = new THREE.Object3D();
        overhead.position.set(0, 400, 0);
        overhead.parent = this.player.object;
        const collect = new THREE.Object3D();
        collect.position.set(40, 82, 94);
        collect.parent = this.player.object;
        this.player.cameras = { front, back, wide, overhead, collect };
        game.activeCamera = this.player.cameras.wide;
        game.cameraFade = 1;
        setTimeout(function () {
            game.activeCamera = game.player.cameras.back;
            game.cameraFade = 0.01;
            setTimeout(function () {
                game.cameraFade = 0.1;
            }, 1500);
        }, 2000);
    }


    loadEnvironment(loader) {
        const game = this;

        console.log("loadingEnv")
        loader.load(
            `${ASSET_PATH}fbx/environment.fbx`,
            function (object) {
                console.log("environment", object)
                game.scene.add(object);
                game.doors = [];
                game.fans = [];

                object.receiveShadow = true;
                object.scale.set(0.8, 0.8, 0.8);
                object.name = "Environment";
                let door = { trigger: null, proxy: [], doors: [] };

                object.traverse(function (child) {
                    if (child.isMesh) {
                        if (child.name.includes("main")) {
                            child.castShadow = true;
                            child.receiveShadow = true;
                        } else if (child.name.includes("mentproxy")) {
                            child.material.visible = false;
                            game.environmentProxy = child;
                        }
                    }
                });
            },
            null,
            this.onError
        )
    }

    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight;
        this.camera.updateProjectionMatrix();

        this.renderer.setSize(window.innerWidth, window.innerHeight);
    }

    animate() {
        const game = this;
        const dt = this.clock.getDelta();
        requestAnimationFrame(function () {
            game.animate();
        });

        if (this.player.mixer != undefined && this.mode == this.modes.ACTIVE) {
            this.player.mixer.update(dt);
        }

        if (this.player.action == "walk") {
            const elapsedTime = Date.now() - this.player.actionTime;
            if (elapsedTime > 1000 && this.player.move.forward > 0)
                this.action = "run";
        }
        if (this.player.move != undefined) {
            if (this.player.move.forward != 0) this.movePlayer(dt);
            this.player.object.rotateY(this.player.move.turn * dt);
        }

        if (
            this.player.cameras != undefined &&
            this.player.cameras.active != undefined
        ) {
            this.camera.position.lerp(
                this.player.cameras.active.getWorldPosition(new THREE.Vector3()),
                this.cameraFade
            );
            let pos;
            if (this.cameraTarget != undefined) {
                this.camera.position.copy(this.cameraTarget.position);
                pos = this.cameraTarget.target;
            } else {
                pos = this.player.object.position.clone();
                pos.y += 60;
            }
            this.camera.lookAt(pos);
        }
        this.renderer.render(this.scene, this.camera);
    }

    onError(error) {
        const msg = console.error(JSON.stringify(error));
        console.error(error.message);
    }
}

let game = new Game();
